/*ResetBootstrap*/
html {
    height: 100%;
}

body {
    font-family: 'PT Sans', sans-serif;
    font-size: 15px;
    color: #2D2E2E;
    background: #f2f7f2;
}

@media(min-width:1660px) {
    .container {
        max-width: 1630px;
    }
}

a {
    color: #E30713;

    &:hover,
    &:focus {
        color: #FF461E;
    }
}

a:focus,
button:focus,
input:focus,
a:active,
button:active,
input:active {
    outline: 0;
}

/*Header*/
.site-header {
    background: #fff;
}

.site-header-row {
    @media(max-width: 767px) {
        flex-direction: column;
        padding: 7px 0;
    }

    @media(min-width: 1660px) {
        height: 120px;
    }

    @media(min-width: 768px) {
        align-items: center;
    }
}

.logo {
    @media(max-width: 767px) {
        width: 150px;
        margin-right: 15px;
    }
}

.mobile-menu-toggler {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    padding: 0;
    border: 0;
    background: transparent;

    &:active {
        span {
            background: #E30713;
        }
    }

    span {
        width: 32px;
        height: 2px;
        background: #4d4d4d;
        border-radius: 3px;

        &:not(:last-child) {
            margin-bottom: 6px;
        }
    }
}

.header-logo-col {
    @media(max-width: 767px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 7px;
    }

    @media(min-width:768px) and (max-width: 1659px) {
        height: 60px;
        margin-top: 10px;
    }
}

.header-city-col {
    @media(min-width: 992px) and (max-width: 1659px) {
        -ms-flex: 0 0 0;
        flex: 0 0 0;
        max-width: inherit;
        margin-left: auto;
    }
}

.header-your-city {
    @media(min-width: 768px) and (max-width: 991px) {
        justify-content: flex-end;
    }

    @media(min-width: 768px) {
        white-space: nowrap;
    }

    display: flex;
    align-items: center;
    color: #E30713;
    font-weight: 700;

    &:hover,
    &:focus {
        text-decoration: none;

        span {
            color: #FF461E;
        }
    }

    span {
        font-weight: 400;
        color: #353B3C;
        margin-right: 5px;
    }

    img {
        min-width: 12px;

        @media(max-width: 1199px) {
            margin-right: 3px;
        }

        @media(min-width: 1200px) {
            margin-right: 15px;
        }

    }
}

.header-nav-col {
    @media(min-width: 992px) {
        -ms-flex: 0 0 0;
        flex: 0 0 0;
        max-width: inherit;
        flex-grow: 1;
    }

    @media(min-width:768px) and (max-width: 1659px) {
        padding: 15px;
        order: 1;
        justify-content: flex-end;
    }

    @media(min-width:768px) and (max-width: 991px) {
        padding-left: 0;
    }
}

.header-login-col {
    @media(min-width: 992px) {
        -ms-flex: 0 0 0;
        flex: 0 0 0;
        max-width: inherit;
    }

    align-items: center;
    white-space: nowrap;

    img {
        margin-right: 10px;
        min-width: 12px;
    }

    a {
        font-size: 15px;
        color: #353B3C;

        &:hover,
        &:focus {
            color: #E30713;
        }
    }

    span {
        font-size: 12px;
        margin: 0 3px;
    }
}

.header-main-navigation {
    @media(max-width: 767px) {
        padding-top: 7px;
    }

    &>ul {
        @media(min-width: 768px) {
            display: flex;
        }

        &>li:not(:last-child) {
            @media(min-width: 768px) and (max-width: 991px) {
                margin-right: 14px;
            }

            @media(min-width: 992px) {
                margin-right: 40px;
            }

        }

        li.active>a {
            color: #E30713;
        }

        a {
            color: #353B3C;
            text-transform: uppercase;

            @media(max-width: 767px) {
                display: block;
                padding: 10px 0;

                &:hover,
                &:focus {
                    text-decoration: none;
                }

                &:active {
                    color: #E30713;
                }
            }

            @media(min-width: 768px) {

                &:hover,
                &:focus {
                    color: #E30713;
                    text-decoration: underline;
                }
            }

        }
    }

    ul {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }
}

.mobile-col {
    @media(max-width: 767px) {
        padding: 0 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.mobile-login-toggler {
    @media(max-width: 767px) {
        display: flex;
    }
}

.login-modal {
    .modal-dialog {
        @media(max-width: 575px) {
            margin: 7px auto;
        }

        @media(max-width: 767px) {
            max-width: 250px;
        }
    }
}

.login-modal-box {
    @media(max-width: 767px) {
        max-width: 200px;
        margin: 0 auto;
    }
}

.login-modal-box-item {
    padding: 15px;
    text-align: center;

    &:not(:last-child) {
        border-bottom: 1px solid #eee;
    }

    a {
        font-size: 16px;
        text-transform: uppercase;
    }
}

/*Home*/
.home-pictured-section {
    @media(max-width: 991px) {
        padding: 15px 0 30px;
        background-size: cover;
    }

    @media(min-width: 992px) and (max-width: 1199px) {
        height: 400px;
    }

    @media(min-width: 1200px) {
        height: 680px;
    }

    background: url(../img/hero-image.jpg) no-repeat 50%;

    @media(max-width: 991px) {
        margin-bottom: 30px;
    }

    @media(min-width: 992px) {
        margin-bottom: 100px;
    }
}

.home-pictured-section-text-wrap {
    @media(max-width: 991px) {
        margin-bottom: 15px;
    }
}

.home-pictured-section-row {
    display: flex;

    @media(min-width: 992px) and (max-width: 1199px) {
        height: 400px;
    }

    @media(min-width: 1200px) {
        height: 680px;
    }

    align-items: center;
}

.home-pictured-section-title {
    font-weight: bold;

    @media(max-width: 767px) {
        font-size: 30px;
        margin-bottom: 5px;
        line-height: 1.3;
    }

    @media(min-width: 768px) {
        font-size: 48px;
        margin-bottom: 20px;
    }
}

.home-pictured-section-text {
    @media(min-width: 576px) {
        font-weight: bold;
        font-size: 21px;
    }

    margin-bottom: 20px;

    p:last-child {
        margin-bottom: 0;
    }
}

.home-pictured-section-more {
    color: #fff;
    font-weight: bold;
    font-size: 12px;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 115px;
    height: 40px;
    background: #E30713;

    &:hover,
    &:focus {
        background: #fff;
        color: #4d4d4d;
        text-decoration: none;
    }
}

.decorated-link-box {
    a {
        @media(max-width: 575px) {
           width:100%;
        }

        overflow: hidden;
        position: relative;
        font-weight: bold;
        font-size: 21px;
        color:#fff;
        display: flex;
        align-items: center;
        background:#363732;
        height: 90px;
        transition:background 0.3s ease;

        &:before {
            content: '';
            position: absolute;

            @media(min-width: 1660px) {
                left: -60px;
            }

            @media(min-width:992px) and (max-width: 1659px) {
                left: -115px;
            }

            @media(min-width:768px) and (max-width: 991px) {
                left: -80px;
            }

            @media(max-width:767px) {
                left: -115px;
            }

            top:0;
            transform: rotate(45deg);
            width:200px;
            height:200px;
            background: #E30713;
        }

        img {
            @media(max-width: 575px) {
                min-width: 30px;
                max-width: 30px;
            }

            @media(max-width: 1659px) {
                margin-left: 15px;
            }

            @media(min-width: 1660px) {
                margin-left: 30px;
            }

            position: relative;
        }

        span {
            position: relative;
            display: flex;
            @media(min-width: 992px){
            justify-content: center; 
                        width: 290px;               
            }
            @media(max-width: 575px){
               font-size:17px;
               padding:0 15px;

            }
            @media(min-width: 576px){
                margin-left: auto;
            }
            
        }

        &:hover,
        &:focus {
            background: #fff;
            color: #333;
        }
    }
}

.home-pictured-section-cat-row {
    @media(min-width: 992px) {
        margin-top: -45px;
    }

    @media(max-width: 991px) {
        margin-top: 15px;
    }
}

.home-pictured-section-cat-col {
    a span{
        @media(min-width: 576px) and (max-width: 767px){
        width:125px;
        }
        @media(min-width: 768px) and (max-width: 991px){
        width:175px;
        }
    }
    @media(max-width: 575px) {
        display: flex;
        justify-content: center;
    }

    &:not(:last-child) {
        @media(max-width: 575px) {
            margin-bottom: 20px;
        }
    }

    &:first-child,
    &:nth-child(2) {
        @media(min-width: 576px) and (max-width:991px) {
            margin-bottom: 30px;
        }
    }
}

.home-main-title {
    font-weight: bold;

    @media(min-width: 1200px) {
        font-size: 80px;
    }

    @media(min-width: 992px) and (max-width: 1199px) {
        font-size: 60px;
    }

    @media(min-width: 768px) and (max-width: 991px) {
        font-size: 45px;
    }

    @media(max-width: 767px) {
        font-size: 30px;
    }

    color: #E30713;
    text-align: center;

    @media(min-width: 576px) {
        margin-bottom: 30px;
    }

    @media(max-width: 575px) {
        margin-bottom: 15px;
    }
}

.home-about-row {
    @media(min-width: 1660px) {
        margin-bottom: 100px;
    }

    @media(min-width: 1200px) and (max-width: 1659px) {
        margin-bottom: 40px;
    }

    @media(max-width: 1199px) {
        margin-bottom: 20px;
    }
}

.home-about-pic-col {
    @media(max-width: 991px) {
        margin-bottom: 15px;
    }

    @media(min-width: 992px) and(max-width: 1199px) {
        margin-bottom: 30px;
    }

    @media(max-width: 1199px) {
        text-align: center;
    }
}

.home-about-text-col {
    color: #000000;

    p:last-child {
        margin-bottom: 0;
    }
}

.home-new-projects-row {
    @media(min-width: 1200px) {
        margin-bottom: 80px;
    }

    @media(max-width:1199px) {
        margin-bottom: 20px;
    }
}

.home-new-projects-big-title {
    @media(min-width: 576px) and (max-width: 1199px) {
        margin-bottom: 20px;
    }

    @media(max-width: 575px) {
        margin-bottom: 15px;
        line-height: 1.2;
    }

    @media(min-width: 1200px) {
        margin-bottom: 60px;
    }

}

.home-new-projects-col {

    &:nth-child(2),
    &:nth-child(3) {
        .home-new-projects-descr-col:before {

            background: #363732;
        }

        .home-new-projects-post-more {
            background: #E30713;
            color: #fff;

            &:hover,
            &:focus {
                background: #fff;
                color: #363732;
                text-decoration: none;
            }
        }
    }

    &:first-child,
    &:nth-child(2) {
        @media(min-width: 1200px) {
            margin-bottom: 80px;
        }
    }

    &:not(:last-child) {
        @media(max-width: 1199px) {
            margin-bottom: 30px;
        }
    }
}

.home-new-projects-pic-col {
    img {
        min-width: 100%;

        @media(min-width: 576px) and (max-width:767px) {
            margin-top: 30px;
        }
    }
}

.home-new-projects-descr-col {
    color: #fff;

    @media(max-width: 575px) {
        padding: 15px 30px;
    }

    @media(min-width: 576px) and (max-width: 1199px) {
        padding: 30px 45px 30px 15px;
    }

    @media(min-width: 1660px) {
        padding: 50px 45px 50px 15px;
    }

    @media(min-width: 1200px) and (max-width: 1659px) {
        padding: 15px 30px 25px 10px;
    }

    &:before {
        content: '';
        position: absolute;

        @media(max-width: 575px) {
            left: 15px;
            right: 15px;
            top: 0;
            bottom: 0;
        }

        @media(min-width: 576px) and (max-width: 767px) {
            left: -150px;
            right: 0;
            top: 0;
            bottom: 0;
        }

        @media(min-width: 768px) and (max-width: 1199px) {
            left: -15px;
            right: 0;
            top: 0;
            bottom: 0;
        }

        @media(min-width: 1200px) and (max-width: 1659px) {
            left: -115px;
            right: 15px;
            top: -15px;
            bottom: 0;
        }

        @media(min-width: 1660px) {
            left: -120px;
            top: -30px;
            bottom: -15px;
            right: 15px;
        }

        z-index: -1;


        background: #E30713;
    }
}

.home-new-projects-title {
    display: block;
    font-weight: bold;

    @media(min-width: 1660px) {
        font-size: 32px;
    }

    @media(min-width: 992px) and (max-width: 1659px) {
        font-size: 20px;
        line-height: 1.3;
    }

    color:#fff;

    margin-bottom: 15px;

    &:hover,
    &:focus {
        color: #fff;
    }
}

.home-new-projects-post-date {
    font-size: 12px;
    margin-bottom: 10px;
}

.home-new-projects-post-author {
    font-size: 12px;

    @media(max-width: 1659px) {
        margin-bottom: 15px;
    }

    @media(min-width: 1660px) {
        margin-bottom: 30px;
    }

}

.home-new-projects-post-text {
    p:last-child {
        margin-bottom: 0;
    }

    @media(max-width: 1659px) {
        margin-bottom: 20px;
    }

    @media(min-width: 1660px) {
        margin-bottom: 40px;
    }
}

.home-new-projects-post-more-wrap {
    display: flex;
    justify-content: flex-end;
}

.home-new-projects-post-more {
    color: #E30713;
    font-weight: bold;
    font-size: 12px;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 145px;
    height: 40px;
    background: #fff;
    transition: background 0.3s ease;

    &:hover,
    &:focus {
        background: #363732;
        color: #fff;
        text-decoration: none;
    }
}

.home-all-projects-link-wrap {
    display: flex;
    justify-content: flex-end;

    @media(min-width: 992px) and (max-width: 1659px) {
        margin-bottom: 40px;
    }

    @media(min-width: 1660px) {
        margin-bottom: 100px;
    }

    @media(max-width: 991px) {
        margin-bottom: 30px;
    }

    a {
        font-weight: bold;
        font-size: 21px;
        color: #2D2E2E;

        &:hover,
        &:focus {
            color: #E30713;
        }
    }
}

