        padding:20px 0;
}
        @media(min-width: 1660px){
    margin-bottom: 120px;
}
}

.catalog-sorting-wrap {
    display: flex;
    align-items: center;
    padding-bottom: 15px;
    border-bottom: 1px solid #E0DBD3;
    @media(max-width: 767px){
        margin-bottom: 20px;
    }
    margin-bottom: 50px;

    button {
        &:first-child {
            @media(min-width:768px){
                margin-left: 100px;
            }
            
        }

        &:not(:last-child) {
            @media(max-width:767px){
            margin-right:30px;
        }
            @media(min-width:768px){
            margin-right: 190px;
        }
        }

        position: relative;
        padding: 0 30px 0 0;
        border:0;
        background: transparent;

        &:hover {
            color: #E30713;
        }

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
            right: 0;
            width: 20px;
            height: 20px;
            background: url(../img/i/down.svg) no-repeat 0 0;
        }

        &.up:before {
            background: url(../img/i/up.svg) no-repeat 0 0;
        }

        &.active {
            font-weight: 700;
            color: #E30713;

            &:before {
                background-position: 0 -20px;
            }
        }
    }
}

.catalog-prev-item {
    margin-bottom: 50px;
    @media(max-width: 991px){
    margin-bottom: 30px;
    }
}

.catalog-prev-item-thumb-wrap {


    position: relative;
    font-size: 20px;
    @media(min-width: 768px){
    padding: 20px;
        overflow: hidden;
            height: 240px;
    }
.catalog-prev-item-thumb-badge-wrap{
@media(max-width: 767px){
    position: absolute;
    top:20px;
    left:20px;
}
}

    .badge {
        position: relative;
        z-index: 1;
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        padding: 5px 10px;
        display: inline-block;
        vertical-align: top;
        margin: 0 0 10px;

        &:not(:last-child) {
            margin-right: 10px;
        }

        border-radius:0;

        &.new {
            background: #fff;
            color: #E30713;
        }

        &.hit {
            background: #E30713;
            color: #fff;
