    color: #888888;
    background: #F3F3F3;
    border: 1px solid #E0DBD3;
    box-sizing: border-box;
    border-radius: 3px;
    height: 60px;
    padding: 20px 55px 20px 20px;
}

.articles-search-submit {
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 100%;
    border: 0;
    background: transparent;
    padding: 0;
    background: url(../img/i/search.svg) no-repeat 50%;
}

.articles-prev-item {
    @media(max-width: 767px){
    flex-direction: column;
    }
    display: flex;
    padding-bottom: 50px;
    margin-bottom: 50px;
    @media(max-width: 575px){
    padding-bottom: 30px;
    margin-bottom: 30px;
    }
    border-bottom: 1px solid #E0DBD3;
}

.articles-prev-pic-col {
    min-width: 240px;
    @media(max-width: 991px){
margin-right:20px;
    }
    @media(max-width: 767px){
        margin:0 0 20px;
        img{
            width:100%;
        }
    }
    margin-right: 70px;
}

.articles-prev-meta-wrap {
    display: flex;
    align-items: center;
    @media(min-width: 576px){
    margin-bottom: 20px;
    }
}

.articles-prev-date {
    color: #E30713;
    font-weight: bold;
    font-size: 17px;
    margin-right: 50px;
    @media(max-width:575px){
    margin-right:10px;
    }
}

.articles-prev-category {
    font-weight: bold;
    font-size: 17px;
    color: #2D2E2E;
}

.articles-prev-title {
    display: block;
    font-weight: bold;
    font-size: 32px;
    margin-bottom: 15px;
    @media(max-width: 575px){
        font-size: 25px;
        margin-bottom: 5px;
    }
}

.articles-prev-descr {
    p:last-child {
        margin-bottom: 0;
    }

    margin-bottom:10px;
}

.articles-prev-more {
    position: relative;
    font-weight: bold;
    font-size: 12px;
    text-transform: uppercase;
    padding-right: 25px;

    &:after {
        content: '';
        position: absolute;
        top: 2px;
        right: 0;
        width: 16px;
        height: 14px;
        background: url(../img/i/arrow-more.svg) no-repeat 0 0;
    }
}
.post-full-row{
margin-bottom: 70px;
@media(max-width: 575px){
margin-bottom:20px;
}
}
.post-full-text-col{
@media(max-width: 1199px){
margin-bottom: 30px;
}
@media(max-width: 575px){
margin-bottom: 15px;
}
}
.post-full-meta-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;
    border-top: 1px solid #E0DBD3;
    border-bottom: 1px solid #E0DBD3;
    margin-bottom: 35px;
    @media(max-width: 575px){
        padding: 10px 0;
        margin-bottom: 10px;
    }
}

.post-full-date {
    color: #E30713;
    font-weight: 700;
    @media(max-width: 575px){
        margin-bottom: 5px;
    }
}

.post-full-category {
    color: #2D2E2E;
    font-weight: 700;
    @media(max-width: 575px){
        margin-bottom: 10px;
    }
}

.post-full-author {
    font-size: 12px;

    img {
        margin-right: 10px;
        min-width: 20px;
    }
}

.post-full-text {
    p{
        margin-bottom:30px;
    }
    p:last-child {
        margin-bottom: 0;
    }

    color:#000;
}
.post-full-pic-col{
img:not(:last-child){
    margin-bottom: 50px;
    @media(max-width: 575px){
        margin-bottom: 15px;
    }
}
}
.post-full-add-comments-section{
background: #fff;
margin-bottom: 60px;
    @media(max-width: 575px){
        margin-bottom: 30px;
    }
}
.post-full-comments{
margin-bottom: 120px;
@media(max-width: 991px){
    margin-bottom: 40px;
}
&~.row .articles-search-form{
    margin-bottom: 70px;
@media(max-width: 991px){
    margin-bottom: 0;
}
}
}
.post-full-comments-item{
padding-bottom: 50px;
&:not(:last-child){
    margin-bottom: 50px;
}
@media(max-width: 575px){
padding-bottom: 15px;
&:not(:last-child){
    margin-bottom: 20px;
}
}
border-bottom:1px solid #E0DBD3;
}
.post-full-comments-item-header{
display: flex;
align-items: center;
margin-bottom: 30px;
@media(max-width: 575px){
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
}
}
.post-full-comment-date{
    font-weight: 700;
    color:#E30713;
    margin-right:70px;
@media(max-width: 575px){
margin:0 0 10px;
}
}
.post-full-comment-author{
    font-size: 12px;

    img {
        margin-right: 10px;
        min-width: 20px;
    }    
}
.post-full-comments-item-content{
    margin-bottom: 20px;
    p:last-child {
        margin-bottom: 0;
    }
@media(max-width: 575px){
margin-bottom:10px;
}  
}
.post-full-comments-item-reply-wrap{
    display: flex;
    justify-content: flex-end;
    a{
        font-weight: bold;
font-size: 12px;
text-transform: uppercase;
    }
}
.catalog-prevs-col{
@media(max-width: 991px){
margin-bottom: 30px;
}
}
/*Footer*/
.site-footer-top-row {
    background: #fff;

    @media(max-width: 767px) {
        padding: 20px 0;
    }

    @media(min-width: 768px) {
        padding: 50px 0;
    }

}

.footer-logo-col {
    @media(min-width: 1200px) and (max-width: 1659px) {
        display: none !important;
    }
}

.footer-bottom-col {
    @media(max-width: 575px) {
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    @media(min-width: 576px) and (max-width: 767px) {
        &:last-child {
            margin-top: 20px;
        }
    }

    @media(min-width: 1200px) and (max-width: 1659px) {
        -ms-flex: 0 0 25%;
        flex: 0 0 25%;
        max-width: 25%;
        margin: 0;
    }

    &.no-title {
        padding-top: 50px;

        @media(min-width: 992px) and (max-width: 1659px) {
            padding-top: 8px;
        }
    }

    ul {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;

        li:not(:last-child) {
            margin-bottom: 10px;
        }

        a {
            color: #353B3C;
        }
    }
}

.footer-list-title {
    @media(max-width: 767px) {
        font-size: 18px;
    }

    @media(min-width: 768px) {
        font-size: 21px;
    }

    font-weight: 700;

    @media(max-width: 767px) {
        margin-bottom: 7px;
    }

    @media(min-width: 768px) and (max-width: 1659px) {
        margin-bottom: 15px;
    }

    @media(min-width: 1660px) {
        height: 50px;
    }
}

.footer-contacts-wrap {
    color: #353B3C;

    a {
        color: #353B3C;
    }



}

.footer-contacts-item {
    &:not(:last-child) {
        margin-bottom: 8px;
    }
}

.footer-our-sites-wrap {
    a {
        display: block;

        &:not(:last-child) {
            margin-bottom: 20px;
        }

        transition:opacity 0.3s ease;

        &:hover,
        &:focus {
            opacity: 0.65;
        }
    }
}

.site-footer-bottom-line {
    background: #E30713;
}

.site-footer-bottom-container {
    max-width: 900px;
    padding: 0 15px;
    margin: 0 auto;
    color: #fff;
    font-size: 12px;
    font-weight: 700;

    a {
        color: #fff;

    }

    .row {

        @media(max-width: 575px) {
            flex-direction: column;
            padding: 15px 0;
        }

        @media(min-width: 576px) {
            height: 60px;
            justify-content: center;
            align-items: center;
        }


    }
}

.site-footer-bottom-col {
    @media(max-width: 575px) {
        padding: 0 15px;

        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    @media(min-width: 576px) and (max-width: 991px) {
        padding: 0 10px;

        &:first-child {
            padding-left: 15px;
        }
    }

    @media(min-width: 992px) {
        padding: 0 30px;
    }


    &:not(:last-child) {
        @media(min-width: 992px) {
            position: relative;

            &:before {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                transform: translate(0, -50%);
                width: 2px;
                height: 20px;
                background: #fff;
                opacity: 0.5;
            }
        }
    }
}

.footer-developer-link {
    display: flex;
    align-items: center;

    img {
        margin-left: 10px;
    }
}
/*Modals*/
.download-project-modal{
    .modal-dialog{
        @media(min-width: 768px){
            max-width: 515px;
        }
    }
    .modal-content{
        border-radius: 0;
        padding:15px 30px 30px 30px;
    }
    .modal-close{
        position: absolute;
        top:20px;
        right:20px;
        padding:0;
        border:0;
        background: transparent;
    }
}
.download-project-modal-title{
    font-weight: bold;
font-size: 32px;
color: #E30713;
padding-right:30px;
margin-bottom:20px;
}
.download-project-modal-counter{
    font-weight: bold;
font-size: 21px;
padding-right:30px;
margin-bottom:40px;
}
.download-project-modal-files-wrap{
    margin-bottom:110px;
}
.download-project-modal-files-item{
    padding:25px 0;
    &:first-child{
        border-top: 1px solid #E0DBD3;
    }
    border-bottom: 1px solid #E0DBD3;
}
.download-project-modal-files-item-title{
    position: relative;
    font-size: 17px;
    cursor: pointer;
    &:after{
        content:'';
        position: absolute;
        right:0;
        width:20px;
        height:20px;
        background: url(../img/i/down.svg) no-repeat 0 0;
    }
    &[aria-expanded="true"]:after{
       background: url(../img/i/up.svg) no-repeat 0 -20px; 
    }
    &:hover{
            color:#E30713;
    }
}
.download-project-modal-files-item-content-wrap{
margin-top:20px;
}
.download-project-modal-files-item-content{
background: #F3F3F3;
border: 1px solid #E0DBD3;
border-radius: 3px;
padding:20px;
}
.download-project-modal-btn-wrap{
display: flex;
justify-content: center;
margin-bottom: 30px;
a{
    width:240px;
}
}